const EVENTS_PER_PAGE = 6

export default function EventsPage() {
  const [currentPage, setCurrentPage] = useState(1)

  const startIndex = (currentPage - 1) * EVENTS_PER_PAGE
  const paginatedEvents = allEvents.slice(startIndex, startIndex + EVENTS_PER_PAGE)
  const totalPages = Math.ceil(allEvents.length / EVENTS_PER_PAGE)

  return (
    <DashboardLayout>
      <DashboardShell>
        <div className="flex items-center justify-between">
          <h2 className="text-3xl font-bold tracking-tight">Events</h2>
          <Button>Create Event</Button>
        </div>

        {/* Filters */}
        <div className="grid gap-4 md:grid-cols-[1fr_200px_200px_auto]">
          <div className="relative">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input type="search" placeholder="Search events..." className="w-full pl-8" />
          </div>
          <Select>
            <SelectTrigger><SelectValue placeholder="Category" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All</SelectItem>
              <SelectItem value="hackathon">Hackathon</SelectItem>
              <SelectItem value="workshop">Workshop</SelectItem>
              <SelectItem value="networking">Networking</SelectItem>
              <SelectItem value="career">Career</SelectItem>
            </SelectContent>
          </Select>
          <Select>
            <SelectTrigger><SelectValue placeholder="Date" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="upcoming">Upcoming</SelectItem>
              <SelectItem value="today">Today</SelectItem>
              <SelectItem value="week">This Week</SelectItem>
              <SelectItem value="month">This Month</SelectItem>
            </SelectContent>
          </Select>
          <Button variant="outline" className="flex gap-2">
            <MapPin className="h-4 w-4" />
            <span>Near Me</span>
          </Button>
        </div>

        {/* Event Cards */}
        <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {paginatedEvents.map((event, index) => (
            <EventCard key={index} {...event} />
          ))}
        </div>

        {/* Pagination Buttons */}
        <div className="flex justify-center mt-8 gap-4">
          <Button
            variant="outline"
            onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
            disabled={currentPage === 1}
          >
            Previous
          </Button>
          <span className="text-sm text-muted-foreground">Page {currentPage} of {totalPages}</span>
          <Button
            variant="outline"
            onClick={() => setCurrentPage((p) => Math.min(p + 1, totalPages))}
            disabled={currentPage === totalPages}
          >
            Next
          </Button>
        </div>
      </DashboardShell>
    </DashboardLayout>
  )
}
